import React, { useEffect, useState } from 'react'
import { Navigate, useNavigate, useParams } from 'react-router-dom'
import { getResellerPackageCommission  ,updateResellerPackageCommission} from '../../../api/login/Login'
import {
    Box,
    Table,
    TableBody,
    TableCell,
    TableHead,
    TableRow,
    MenuItem,
    Select,
    TextField,
    TableContainer,
    Paper,
    Typography,
    Button
  } from "@mui/material";
import toastMessage from '../../../utils/toastMessage';
import Loader from '../../../common/loader/Loader';
import { ToastContainer } from 'react-toastify';


  const CustomTextField = (props) => (
    <TextField
      variant="outlined"
      size="small"
      fullWidth
      sx={{
        "& .MuiOutlinedInput-root": {
          height: 41,
          "& .MuiOutlinedInput-input": {
            py: 1,
          },
          "&:hover fieldset": { borderColor: "var(--bs-primary1)" },
          "&.Mui-focused fieldset": { borderColor: "var(--bs-primary1)" },
        },
      }}
      {...props}
    />
  );



const ResellerPackageUpdate = () => {
   const { id } =   useParams()
   const [data , setData] = useState()
   const naviagte = useNavigate()
   const [rows, setRows] = useState(); 
   const [loading , setLoading] = useState(false)
   

   const getPackage = async()=>{
     setLoading(true)
      try {
        let res = await getResellerPackageCommission(id)
          if(res?.data?.statusCode === '200')
          {
            setRows(res?.data?.data?.list)
            setData(res?.data?.data)
          }
      } catch (error) {
        
      } finally{
        setLoading(false)
      }
    
   }


   useEffect(()=>{
    getPackage()


   },[])


     // generic handler so you don’t write 10 onChange callbacks
  const handleChange = (idx, field) => e => {
    const updated = [...rows];
    updated[idx] = { ...updated[idx], [field]: e.target.value };
    setRows(updated);
  };

  


  const handleSubmit = async()=>{
    let body = {
        ...data,
        list : rows,
     }
     setLoading(true)
       try {
         let res  = await updateResellerPackageCommission(id ,body )
          if(res?.data?.statusCode === '200')
          {
            setTimeout(()=>{
                naviagte(-1)
            },500)
          }
         toastMessage(res?.data?.error , res?.data?.message)
       } catch (error) {
         
       } finally {
         setLoading(false)
       }
  }
  return (
    <>
     {loading && <Loader/>}

     
        <Paper
        sx={{
          padding: 3,
          borderRadius: 2,
          // border: "1px solid #ccc",
          marginTop: 4,
          // background: "linear-gradient(135deg,  #F87A1F ,#FFCC29)",
          boxShadow: "0 4px 12px rgba(0, 0, 0, 0.1)",
        }}
      >
   <Box>
      <Typography sx={{fontSize:'20px' , color:'var(--bs-nav)' , mb:2}}>
        {data?.name}
      </Typography>
   </Box>
     
    <Box sx={{ overflowX: "auto" }}>
            <TableContainer component={Box} elevation={3}>
             
      <Table size="small">
        <TableHead sx={{ backgroundColor: '#f5f5f5' }}>
          <TableRow>
          <TableCell><strong>Serial&nbsp;No</strong></TableCell>
            <TableCell><strong>Start&nbsp;Amt</strong></TableCell>
            <TableCell><strong>End&nbsp;Amt</strong></TableCell>
            <TableCell><strong>Charge</strong></TableCell>
            <TableCell><strong>Charge&nbsp;Type </strong></TableCell>
            <TableCell><strong>Reseller&nbsp;Commission</strong></TableCell>
            <TableCell><strong>Reseller&nbsp;Commission&nbsp;Type </strong></TableCell>
          </TableRow>
        </TableHead>

        <TableBody>
          {rows?.map((row, idx) => (
            <TableRow key={row._id}>
              {/* start_amt (read‑only example) */}
              <TableCell>
            
                  {idx +1}
               
               
              </TableCell>
              <TableCell>
                <CustomTextField
                  value={row.start_amt}
                  disabled
                />
              </TableCell>

              {/* end_amt (read‑only example) */}
              <TableCell>
                <CustomTextField
                  value={row.end_amt}
                  disabled
                />
              </TableCell>

              {/* charge (editable) */}
              <TableCell>
                <CustomTextField
                  type="number"
                  value={row.charge}
                  disabled
                //   onChange={handleChange(idx, "charge")}
                //   inputProps={{ min: 0 }}
                />
              </TableCell>

              {/* charge_type –‑‑ DISABLED AS REQUESTED */}
              <TableCell>
                <CustomTextField
                  value={row.charge_type}
                  disabled                   // <‑‑ key line
                />
              </TableCell>

              {/* reseller_user_comm (editable) */}
              <TableCell>
                <CustomTextField
                  type="number"
                  value={row.reseller_user_comm}
                  onChange={handleChange(idx, "reseller_user_comm")}
                  inputProps={{ min: 0 }}
                />
              </TableCell>

              {/* reseller_user_comm_type (editable select) */}
              <TableCell>
                <Select
                  size="small"
                  fullWidth
                  value={row.reseller_user_comm_type}
                  onChange={handleChange(idx, "reseller_user_comm_type")}
                  sx={{
                    fontSize: "14px",
                    "& .MuiOutlinedInput-notchedOutline": {
                      borderColor: "#ccc",
                    },
                    "&:hover .MuiOutlinedInput-notchedOutline": {
                      borderColor: "var(--bs-primary1)",
                    },
                    "&.Mui-focused .MuiOutlinedInput-notchedOutline": {
                      borderColor: "var(--bs-primary1)",
                    },
                  }}
              
                >
                  <MenuItem value="Flat" style={{color:"var(--bs-nav)" , fontSize:'14px'}}>Flat</MenuItem>
                  <MenuItem value="Percent"style={{color:"var(--bs-nav)" , fontSize:'14px'}}>Percent</MenuItem>
                </Select>
              </TableCell>
            </TableRow>
          ))}
        </TableBody>
      </Table>
      </TableContainer>
    </Box>
   <Box >
    

      <Button  variant="contained" onClick={handleSubmit} color="primary"      sx={{
                                    borderColor: 'var(--bs-primary1)', // Custom colors
                                    width:"200px",
                                    backgroundColor: 'var(--bs-primary1)', // Custom colors
                                    color: "white",
                                    mt:2,
                                    ml:2,
                                  }} >
                                    Submit
                                </Button>
                                </Box>
    </Paper>
  
    </>
  )
}

export default ResellerPackageUpdate